import { notification } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons'
export const openNotification = (title, description) => {
    notification.open({
        message: title,
        description: description,
        duration: 3,
        icon: <ExclamationCircleOutlined style={{ color: '#f5222d' }} />,
        style: {
            border: '2px solid #f5222d',
            borderRadius: '8px',
            background: '#fff7f8',
        },
    });
};
export const openNotificationWithIcon = (type, title, description) => {
    notification[type]({
        message: title,
        description:
            description,
        duration: 3
    });
};
export const validateAmount = (inputAmount) => {
    // Kim tra xem inputAmount c phi l s khng
    if (isNaN(inputAmount)) {
      console.error('Input amount is not a valid number.');
      return false;
    }
  
    // Chuyn i inputAmount t chui sang s
    const amount = parseFloat(inputAmount);
  
    // Kim tra xem inputAmount c ln hn 0 khng
    if (amount <= 0) {
      console.error('Input amount must be greater than 0.');
      return false;
    }
  
    // Kim tra xem inputAmount c l null hay khng
    if (inputAmount === null) {
      console.error('Input amount cannot be null.');
      return false;
    }
  
    // Nu tt c cc iu kin u ng, tr v true
    return true;
  };